import React, { useState } from 'react';
import { PlayerHand, DiscardPile, CurrentCard, ColorPicker } from './game';
import { Button } from './ui';
import { Card as CardType, CardColor } from '@/types';
import { createStandardDeck } from '@/utils';

export const GameDemo: React.FC = () => {
  const [selectedCardIndex, setSelectedCardIndex] = useState<number | undefined>(undefined);
  const [isColorPickerOpen, setIsColorPickerOpen] = useState(false);
  const [selectedColor, setSelectedColor] = useState<CardColor>(CardColor.RED);
  const [currentCardIndex, setCurrentCardIndex] = useState(0);

  // 
  const allCards = createStandardDeck();
  const playerCards = allCards.slice(0, 7);
  const discardCards = allCards.slice(50, 55);
  const currentCard = allCards[currentCardIndex];

  // 3
  const playableCards = new Set(playerCards.slice(0, 3).map(card => card.id));

  // 
  const handleCardClick = (card: CardType, index: number) => {
    setSelectedCardIndex(index);
    console.log(':', card, ':', index);
  };

  // 
  const handleCardPlay = (card: CardType, index: number) => {
    console.log(':', card, ':', index);
    // 
    if (card.type === 'wild' || card.type === 'wild_draw_four') {
      setIsColorPickerOpen(true);
    }
  };

  // 
  const handleColorSelect = (color: CardColor) => {
    setSelectedColor(color);
    setIsColorPickerOpen(false);
    console.log(':', color);
  };

  // 
  const handleNextCard = () => {
    setCurrentCardIndex((prev) => (prev + 1) % allCards.length);
  };

  return (
    <div data-testid="game-demo" className="max-w-7xl mx-auto p-6 space-y-8">
      <div className="text-center">
        <h1 className="text-3xl font-bold text-gray-800 mb-2">
           
        </h1>
        <p className="text-gray-600">
          UNO
        </p>
      </div>

      {/* PlayerHand */}
      <section className="bg-white rounded-lg p-6 shadow-lg">
        <h2 className="text-2xl font-semibold mb-4"> PlayerHand </h2>
        
        {/*  */}
        <div className="space-y-6">
          <div>
            <h3 className="text-lg font-medium mb-3"></h3>
            <PlayerHand
              cards={playerCards}
              isCurrentPlayer={true}
              selectedCardIndex={selectedCardIndex}
              playableCards={playableCards}
              layout="horizontal"
              onCardClick={handleCardClick}
              onCardPlay={handleCardPlay}
            />
          </div>

          <div>
            <h3 className="text-lg font-medium mb-3"></h3>
            <PlayerHand
              cards={playerCards.slice(0, 5)}
              isCurrentPlayer={false}
              layout="fan"
              size="small"
            />
          </div>

          <div>
            <h3 className="text-lg font-medium mb-3"></h3>
            <PlayerHand
              cards={playerCards}
              isCurrentPlayer={false}
              isVisible={false}
              layout="stack"
              size="small"
            />
          </div>
        </div>
      </section>

      {/* DiscardPile */}
      <section className="bg-white rounded-lg p-6 shadow-lg">
        <h2 className="text-2xl font-semibold mb-4"> DiscardPile </h2>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <h3 className="text-lg font-medium mb-3"></h3>
            <DiscardPile
              cards={discardCards}
              currentCard={currentCard}
              onPileClick={() => console.log('')}
            />
          </div>
          
          <div>
            <h3 className="text-lg font-medium mb-3"></h3>
            <DiscardPile
              cards={[]}
              onPileClick={() => console.log('')}
            />
          </div>
        </div>
      </section>

      {/* CurrentCard */}
      <section className="bg-white rounded-lg p-6 shadow-lg">
        <h2 className="text-2xl font-semibold mb-4"> CurrentCard </h2>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <h3 className="text-lg font-medium mb-3"></h3>
            <CurrentCard
              card={currentCard}
              effectiveColor={selectedColor}
              isActive={true}
              onCardClick={() => console.log('')}
            />
          </div>
          
          <div>
            <h3 className="text-lg font-medium mb-3"></h3>
            <CurrentCard
              card={currentCard}
              showDetails={false}
              size="medium"
            />
          </div>
        </div>

        <div className="mt-4 text-center">
          <Button onClick={handleNextCard}>
            
          </Button>
        </div>
      </section>

      {/* ColorPicker */}
      <section className="bg-white rounded-lg p-6 shadow-lg">
        <h2 className="text-2xl font-semibold mb-4"> ColorPicker </h2>
        
        <div className="space-y-4">
          <div className="flex items-center gap-4">
            <Button 
              variant="primary"
              onClick={() => setIsColorPickerOpen(true)}
            >
              
            </Button>
            <div className="flex items-center gap-2">
              <span>:</span>
              <div
                className={`w-6 h-6 rounded-full border-2 border-white shadow-md ${
                  selectedColor === 'red' ? 'bg-red-500' :
                  selectedColor === 'blue' ? 'bg-blue-500' :
                  selectedColor === 'green' ? 'bg-green-500' :
                  selectedColor === 'yellow' ? 'bg-yellow-500' :
                  'bg-gray-500'
                }`}
              />
              <span className="font-medium">
                {selectedColor === 'red' ? '' :
                 selectedColor === 'blue' ? '' :
                 selectedColor === 'green' ? '' :
                 selectedColor === 'yellow' ? '' : ''}
              </span>
            </div>
          </div>
        </div>
      </section>

      {/*  */}
      <section className="bg-white rounded-lg p-6 shadow-lg">
        <h2 className="text-2xl font-semibold mb-4"> </h2>
        
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/*  */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium"></h3>
            <PlayerHand
              cards={allCards.slice(20, 25)}
              isCurrentPlayer={false}
              layout="fan"
              size="small"
            />
            <PlayerHand
              cards={allCards.slice(30, 37)}
              isCurrentPlayer={false}
              isVisible={false}
              layout="stack"
              size="small"
            />
          </div>

          {/*  */}
          <div className="flex flex-col items-center space-y-4">
            <CurrentCard
              card={currentCard}
              effectiveColor={selectedColor}
              isActive={true}
              size="medium"
            />
            <DiscardPile
              cards={discardCards}
              currentCard={currentCard}
              size="medium"
            />
          </div>

          {/*  */}
          <div>
            <h3 className="text-lg font-medium mb-4"></h3>
            <PlayerHand
              cards={playerCards}
              isCurrentPlayer={true}
              selectedCardIndex={selectedCardIndex}
              playableCards={playableCards}
              layout="horizontal"
              onCardClick={handleCardClick}
              onCardPlay={handleCardPlay}
            />
          </div>
        </div>
      </section>

      {/*  */}
      <ColorPicker
        isOpen={isColorPickerOpen}
        selectedColor={selectedColor}
        onColorSelect={handleColorSelect}
        onCancel={() => setIsColorPickerOpen(false)}
      />
    </div>
  );
}; 