import { Card } from '@/types'

/**
 * Fisher-Yates
 * 
 */
export function shuffleArray<T>(array: T[]): T[] {
  const shuffled = [...array] // 
  
  for (let i = shuffled.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1))
    ;[shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]]
  }
  
  return shuffled
}

/**
 * 
 */
export function shuffleDeck(deck: Card[]): Card[] {
  return shuffleArray(deck)
}

/**
 * 
 * @param min 
 * @param max 
 */
export function randomInt(min: number, max: number): number {
  return Math.floor(Math.random() * (max - min + 1)) + min
}

/**
 * 
 */
export function randomChoice<T>(array: T[]): T {
  if (array.length === 0) {
    throw new Error('Cannot choose from empty array')
  }
  const index = randomInt(0, array.length - 1)
  return array[index]
}

/**
 * 
 */
export function randomChoices<T>(array: T[], count: number): T[] {
  if (count > array.length) {
    throw new Error('Cannot choose more elements than available')
  }
  
  const shuffled = shuffleArray(array)
  return shuffled.slice(0, count)
}

/**
 * 
 * @param probability true0-1
 */
export function randomBoolean(probability: number = 0.5): boolean {
  return Math.random() < probability
}

/**
 * 
 * @param min 
 * @param max 
 */
export function randomFloat(min: number, max: number): number {
  return Math.random() * (max - min) + min
}

/**
 * 
 * @param items 
 * @param weights 
 */
export function weightedRandomChoice<T>(items: T[], weights: number[]): T {
  if (items.length !== weights.length) {
    throw new Error('Items and weights arrays must have the same length')
  }
  
  if (items.length === 0) {
    throw new Error('Cannot choose from empty array')
  }
  
  const totalWeight = weights.reduce((sum, weight) => sum + weight, 0)
  if (totalWeight <= 0) {
    throw new Error('Total weight must be positive')
  }
  
  let random = Math.random() * totalWeight
  
  for (let i = 0; i < items.length; i++) {
    random -= weights[i]
    if (random <= 0) {
      return items[i]
    }
  }
  
  // 
  return items[items.length - 1]
}

/**
 * 
 */
export class SeededRandom {
  private seed: number

  constructor(seed: number) {
    this.seed = seed
  }

  /**
   * 0-1
   */
  next(): number {
    this.seed = (this.seed * 9301 + 49297) % 233280
    return this.seed / 233280
  }

  /**
   * 
   */
  nextInt(min: number, max: number): number {
    return Math.floor(this.next() * (max - min + 1)) + min
  }

  /**
   * 
   */
  shuffle<T>(array: T[]): T[] {
    const shuffled = [...array]
    
    for (let i = shuffled.length - 1; i > 0; i--) {
      const j = this.nextInt(0, i)
      ;[shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]]
    }
    
    return shuffled
  }
}

/**
 * 
 */
export function validateShuffleRandomness<T>(
  original: T[],
  shuffled: T[],
  threshold: number = 0.3
): boolean {
  if (original.length !== shuffled.length) {
    return false
  }
  
  if (original.length < 2) {
    return true
  }
  
  // 
  let changedPositions = 0
  for (let i = 0; i < original.length; i++) {
    if (original[i] !== shuffled[i]) {
      changedPositions++
    }
  }
  
  const changeRatio = changedPositions / original.length
  return changeRatio >= threshold
}

/**
 * 
 */
export function multiShuffle<T>(array: T[], times: number = 3): T[] {
  let result = [...array]
  
  for (let i = 0; i < times; i++) {
    result = shuffleArray(result)
  }
  
  return result
} 