import { Card } from './Card';
import { CardColor, CardType } from '../types';
import { shuffleArray } from '../utils/shuffleUtils';

/**
 * UNO
 * 
 */
export class Deck {
  private cards: Card[] = [];

  constructor(cards: Card[] = []) {
    this.cards = [...cards];
  }

  /**
   * UNO
   */
  static createStandardDeck(): Deck {
    const cards: Card[] = [];
    const colors: CardColor[] = [CardColor.RED, CardColor.YELLOW, CardColor.GREEN, CardColor.BLUE];

    // 0-9011-92
    colors.forEach(color => {
      // 01
      cards.push(new Card(CardType.NUMBER, color, 0));
      
      // 1-92
      for (let num = 1; num <= 9; num++) {
        cards.push(new Card(CardType.NUMBER, color, num));
        cards.push(new Card(CardType.NUMBER, color, num));
      }
    });

    // 2
    colors.forEach(color => {
      // 
      cards.push(new Card(CardType.SKIP, color));
      cards.push(new Card(CardType.SKIP, color));
      
      // 
      cards.push(new Card(CardType.REVERSE, color));
      cards.push(new Card(CardType.REVERSE, color));
      
      // +2
      cards.push(new Card(CardType.DRAW_TWO, color));
      cards.push(new Card(CardType.DRAW_TWO, color));
    });

    // 4
    for (let i = 0; i < 4; i++) {
      cards.push(new Card(CardType.WILD));
    }

    // +44
    for (let i = 0; i < 4; i++) {
      cards.push(new Card(CardType.WILD_DRAW_FOUR));
    }

    return new Deck(cards);
  }

  /**
   * 
   */
  shuffle(): void {
    this.cards = shuffleArray(this.cards);
  }

  /**
   * 
   */
  drawCard(): Card | null {
    return this.cards.pop() || null;
  }

  /**
   * 
   */
  drawCards(count: number): Card[] {
    const drawnCards: Card[] = [];
    for (let i = 0; i < count && this.cards.length > 0; i++) {
      const card = this.drawCard();
      if (card) {
        drawnCards.push(card);
      }
    }
    return drawnCards;
  }

  /**
   * 
   */
  addCard(card: Card): void {
    this.cards.unshift(card);
  }

  /**
   * 
   */
  addCards(cards: Card[]): void {
    this.cards.unshift(...cards);
  }

  /**
   * 
   */
  addCardToTop(card: Card): void {
    this.cards.push(card);
  }

  /**
   * 
   */
  getCount(): number {
    return this.cards.length;
  }

  /**
   * 
   */
  isEmpty(): boolean {
    return this.cards.length === 0;
  }

  /**
   * 
   */
  peek(): Card | null {
    return this.cards[this.cards.length - 1] || null;
  }

  /**
   * 
   */
  peekBottom(): Card | null {
    return this.cards[0] || null;
  }

  /**
   * 
   */
  getAllCards(): Card[] {
    return [...this.cards];
  }

  /**
   * 
   */
  clear(): void {
    this.cards = [];
  }

  /**
   * 
   * 
   */
  refillFromDiscardPile(discardPile: Card[]): void {
    if (discardPile.length <= 1) {
      return; // 
    }

    // 
    const cardsToRefill = discardPile.slice(0, -1);
    
    // 
    const resetCards = cardsToRefill.map(card => {
      if (card.isWildCard()) {
        return new Card(card.type, CardColor.WILD, card.value);
      }
      return card.clone();
    });

    // 
    this.addCards(resetCards);
    this.shuffle();
  }

  /**
   * 
   */
  contains(targetCard: Card): boolean {
    return this.cards.some(card => card.equals(targetCard));
  }

  /**
   * 
   */
  removeCard(targetCard: Card): boolean {
    const index = this.cards.findIndex(card => card.equals(targetCard));
    if (index !== -1) {
      this.cards.splice(index, 1);
      return true;
    }
    return false;
  }

  /**
   * 
   */
  getStatistics(): {
    total: number;
    numberCards: number;
    actionCards: number;
    wildCards: number;
    byColor: Record<string, number>;
  } {
    const stats = {
      total: this.cards.length,
      numberCards: 0,
      actionCards: 0,
      wildCards: 0,
      byColor: {} as Record<string, number>
    };

    this.cards.forEach(card => {
      if (card.isNumberCard()) {
        stats.numberCards++;
      } else if (card.isActionCard()) {
        stats.actionCards++;
      } else if (card.isWildCard()) {
        stats.wildCards++;
      }

      const color = card.color || 'wild';
      stats.byColor[color] = (stats.byColor[color] || 0) + 1;
    });

    return stats;
  }

  /**
   * 
   */
  clone(): Deck {
    const clonedCards = this.cards.map(card => card.clone());
    return new Deck(clonedCards);
  }
} 