/**
 * 
 */
export enum CardType {
  NUMBER = 'number',
  SKIP = 'skip',
  REVERSE = 'reverse',
  DRAW_TWO = 'draw_two',
  WILD = 'wild',
  WILD_DRAW_FOUR = 'wild_draw_four'
}

/**
 * 
 */
export enum CardColor {
  RED = 'red',
  BLUE = 'blue',
  GREEN = 'green',
  YELLOW = 'yellow',
  WILD = 'wild'
}

/**
 * 
 */
export interface Card {
  /**  */
  id: string;
  /**  */
  type: CardType;
  /**  */
  color: CardColor;
  /**  (0-9) */
  value?: number;
}

/**
 * 
 */
export interface CreateCardParams {
  type: CardType;
  color: CardColor;
  value?: number;
}

/**
 * 
 */
export interface CardMatchRule {
  /**  */
  canPlay: boolean;
  /**  */
  reason?: string;
}

/**
 * 
 */
export interface CardEffect {
  /**  */
  skipPlayers?: number;
  /**  */
  drawCards?: number;
  /**  */
  reverse?: boolean;
  /**  */
  chooseColor?: boolean;
} 