import { Card } from './Card'

/**
 * 
 */
export enum PlayerType {
  HUMAN = 'human',
  AI = 'ai'
}

/**
 * AI
 */
export enum AIDifficulty {
  EASY = 'easy',
  MEDIUM = 'medium',
  HARD = 'hard'
}

/**
 * 
 */
export interface Player {
  /**  */
  id: string;
  /**  */
  name: string;
  /**  */
  type: PlayerType;
  /**  */
  hand: Card[];
  /** AI */
  isAI: boolean;
  /** AIAI */
  aiDifficulty?: AIDifficulty;
  /** URL */
  avatar?: string;
  /**  */
  score: number;
  /** UNO */
  hasCalledUno: boolean;
  /**  */
  status: PlayerStatus;
}

/**
 * 
 */
export enum PlayerStatus {
  WAITING = 'waiting',
  PLAYING = 'playing',
  THINKING = 'thinking',
  FINISHED = 'finished'
}

