import { Card, CardColor } from './Card'
import { Player } from './Player'

/**
 * 
 */
export enum GamePhase {
  SETUP = 'setup',
  PLAYING = 'playing',
  PAUSED = 'paused',
  FINISHED = 'finished'
}

/**
 * 
 */
export enum GameDirection {
  CLOCKWISE = 1,
  COUNTERCLOCKWISE = -1
}

/**
 * 
 */
export interface GameState {
  /** ID */
  gameId: string;
  /**  */
  players: Player[];
  /**  */
  currentPlayerIndex: number;
  /**  */
  direction: GameDirection;
  /**  */
  deck: Card[];
  /**  */
  discardPile: Card[];
  /**  */
  currentCard: Card;
  /**  */
  gamePhase: GamePhase;
  /**  */
  winner?: Player;
  /** +2+4 */
  drawCount: number;
  /**  */
  chosenColor?: CardColor;
  /**  */
  startTime: Date;
  /**  */
  endTime?: Date;
  /**  */
  turnCount: number;
}

